

.App{
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(170, 160, 160);
}
.search-autocomplete-container{
  padding: 2rem;
}
.search-autocomplete-container input{
  outline: none;
  padding: 1rem 2rem;
  text-align: center;
  font-size: 1rem;
  
}
.tic-tac-toe-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
}
.squre{
  border: 1px solid red;
  float: left;
  font-size: 40px;
  height: 100px;
  padding: 0px;
  text-align: center;
  width: 100px;
  margin-right: 1px;
  margin-top: -1px;
  cursor: pointer;
}

.restart{
  padding:1rem 2rem;
  font-size: 18px;
  font-weight: bold;
  outline: none;
  border: none;
  cursor: pointer;
}